<template>
	<div class="box">
		<div class="model" style="width:500px;height:281px;overflow:hidden;">
			<video style="display:block;width:500px;height:281px;" id="media" src="../../assets/video/videoIndex1.mp4"></video>
		</div>
		<el-row>	
			<el-button size="small" @click="clickPlay">播放</el-button>
			<el-button size="small" @click="clickPause">暂停</el-button>
			<el-button size="small" @click="clickVolumePlus">音量+</el-button>
			<el-button size="small" @click="clickVolumeMinus">音量-</el-button>
			<el-button size="small" @click="clickVolumeSwitch">静音</el-button>
		</el-row>	

	</div>

</template>

<script>
var Media;
export default {
	name: 'testF',
	data() {
		return {
			vol: 0.5
		}
	},
	created() {

	},
	mounted() {
		Media = document.getElementById("media");

	},
	methods: {
		clickPlay () {
			//播放
			Media.play();
		},
		clickPause() {
			//暂停
			Media.pause();
		},
		//音量加
		clickVolumePlus() {
			if(this.vol <= 0.9) {
				this.vol += 0.1;
			} else {
				this.$message({
					message: '音量已达到最大！',
					type: 'warning'
				});
			}
			Media.volume = this.vol;
		},
		//音量减
		clickVolumeMinus() {
			if(this.vol >= 0.2) {
				this.vol -= 0.1;
			} else {
				this.$message({
					message: '音量已是最小！',
					type: 'warning'
				});
			}
			Media.volume = this.vol;
		},
		//静音
		clickVolumeSwitch() {
			this.vol = 0;
			Media.volume = this.vol;
		}
	}
}
</script>

<style scoped>

</style>


